#citySearch{
  position: relative;
  width: 250px;
  margin-left:auto;
  margin-right:auto;
  text-align:left;
}

/* Style the search bar */
.city {
  width: 100%; /* Full width of the container */
  padding: 10px; /* Add some padding for usability */
  border-radius: 5px;
  box-sizing: border-box; /* Include padding/borders in width */
}

.suggestions {
  list-style: none; /* Remove bullets */
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
  position: absolute; /* Position relative to the search bar */
  top: 100%; /* Place directly below the search bar */
  left: 0; /* Align left edge with search bar */
  width: 100%; /* Match width of search bar */
  background-color: #f9f9f9;
  border-radius: 0 0 5px 5px; /* Rounded bottom corners */

  z-index: 1000; /* Ensure dropdown appears above other elements */
}

/* Style each dropdown item */
.suggestions li {
  padding: 7px; /* Add spacing inside each item */
  cursor: pointer; /* Make it look clickable */
  transition: background-color 0.2s ease;
}
.suggestions li:hover {
  background-color: #A7D7D4; /* Highlight on hover */
}

#numberSearch{
  position: relative;
  width: 200px;
  margin-left:auto;
  margin-right:auto;
}

.eventNumber{
  width: 100%; /* Full width of the container */
  padding: 10px; /* Add some padding for usability */
  border-radius: 5px;
  box-sizing: border-box; /* Include padding/borders in width */
}

li.event {
  border: 2px solid rgb(223, 223, 223); /* Dark grey on the left */
  padding-top: 10px; /* Adjusted padding */
  padding-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  max-width: 675px; /* Ensure this max-width is followed */
  width: 100%; /* Ensure the width is always 100% of the container, respecting max-width */
  box-sizing: border-box; /* Include padding and border in the width */
  list-style: none;
  overflow: hidden; /* Prevent content from spilling out */
}
h3,p{
